.pop {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 100;
  background-color: black;
  opacity: 0.9;
  color: white;
}

.pop .popBox {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 20rem;
  width: 30rem;
  border: 0.2rem solid white;
  border-radius: 1rem;
}

.pop .popBox .messege {
  font-size: 3rem;
}

.pop .popBox #close {
  position: absolute;
  font-size: 2rem;
  top: 1rem;
  right: 1rem;
  cursor: pointer;
}
