@import "../colors";
@import "../utils";

.adPro {
  padding: 5rem 0;
  @include flex(center);
  gap: 2rem;
  flex-wrap: wrap !important;
  @include HW(auto, 100%);
  .proBox {
    border-radius: 1rem;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
      rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
      rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    flex: 1 1 30rem;
    max-width: 40rem;
    h5 {
      font-size: 1.5rem;
    }
    span {
      font-size: 2.5rem;
      color: orange;
    }
    padding: 1rem;
    .i {
      cursor: pointer;
      font-size: 1.5rem;
      margin: 0 1rem;
    }
  }
}
