@import "../colors";
@import "../utils";
header {
  // background-image: linear-gradient(
  //   180deg,
  //   #ff7f49 0,
  //   #ff6f46 16.67%,
  //   #fe5942 33.33%,
  //   #f23c3c 50%,
  //   #e50b37 66.67%,
  //   #db0036 83.33%,
  //   #d20038 100%
  // );
  background-image: linear-gradient(
    to right,
    #ff512f 0%,
    #f09819 51%,
    #ff512f 100%
  );
  // background-color: orangered;
  color: $white;
  @include flex(center);
  @include HW(8rem, 100%);
  nav {
    width: 80%;
    margin: auto;
    h2 {
      font-size: 4rem;
    }
    @include flex(space-between);
    ul {
      @include flex(center);
      li {
        position: relative;
        margin: 0rem 2rem;
        .a {
          color: white;
        }
        a {
          font-size: 1.8rem;
          cursor: pointer;
        }
        &::after {
          position: absoute;
          content: "";
          width: 0%;
          height: 0.3rem;
          background-color: $sec;
          display: block;
          bottom: -1.5rem;
          left: 0;
          transition: width 0.5s;
        }
        &:hover {
          &::after {
            width: 100%;
          }
        }
      }
    }
    .tPart {
      @include flex(center);
      #search {
        border-radius: 0.5rem;
        padding: 0.5rem;
      }
      .searchIcon {
        font-size: 2rem;
        position: absolute;
        top: 3px;
        cursor: pointer;
        right: 0;
        color: purple;
      }
    }
    .btns {
      @include flex(center);
      .getS {
        &:nth-child(1) {
          background-color: white;
          color: black;
          &:hover {
            color: #d878dd;
          }
        }
        margin-left: 1.5rem;
        background-color: $sec;
        color: $white;
        padding: 0.6rem 1rem;
        font-size: 1.5rem;
        border-radius: 0.5rem;
        background: #d878dd;
        // background: -moz-linear-gradient(-45deg, #e2a9e5 15%, #2b94e5 100%);
        // background: -webkit-linear-gradient(-45deg, #e2a9e5 15%, #2b94e5 100%);
        margin: 10px;
        // padding: 15px 45px;
        text-align: center;
        text-transform: uppercase;
        transition: 0.5s;
        background-size: 200% auto;
        color: white;
        box-shadow: 0 0 20px #eee;
        border-radius: 10px;
        display: block;
        &:hover {
          background-position: right center; /* change the direction of the change here */
          color: #fff;
          text-decoration: none;
        }
        text-transform: capitalize !important;
      }
    }
    .menu {
      display: none;
      // width: 70rem;
      flex: 1 1 70rem;
      #menu {
        font-size: 4rem;
        cursor: pointer;
        display: none;
        float: right;
      }
    }
  }
  .logout {
    cursor: pointer;
    margin-left: 1.5rem;
    background-color: $sec;
    color: $white;
    padding: 0.6rem 1rem;
    font-size: 1.5rem;
    border-radius: 0.5rem;
    background: white;
    // background: -moz-linear-gradient(-45deg, #e2a9e5 15%, #2b94e5 100%);
    // background: -webkit-linear-gradient(-45deg, #e2a9e5 15%, #2b94e5 100%);
    margin: 10px;
    // padding: 15px 45px;
    text-align: center;
    text-transform: uppercase;
    transition: 0.5s;
    background-size: 200% auto;
    color: black;
    box-shadow: 0 0 20px #eee;
    border-radius: 10px;
    display: block;
    text-transform: capitalize !important;
    &:hover {
      background-position: right center; /* change the direction of the change here */
      color: #d878dd;
      text-decoration: none;
    }
  }
}

@media screen and (max-width: 768px) {
  header {
    height: 16rem;
    transition: all 0.4s;
    nav {
      flex-wrap: wrap;
      justify-content: center;
      // display: block;
      ul {
        display: none;
        padding-top: 8rem;
        .a {
          color: black !important;
          font-size: 3rem;
        }
        li {
          padding: 2rem;
        }
        z-index: 100;
        position: absolute !important;
        height: 100vh !important;
        transform: translateX(150%);
        width: 50vw;
        background-color: white;
        right: 0;
        top: 0;
        color: black;
        display: block;
        transition: all 0.5s;
      }
      .btns {
        // transform: translateX(150%);
        position: absolute !important;
        right: 0%;
        top: 27%;
        z-index: 101;
        // top: 50rem;
        // display: none !important;
      }
      .menu {
        display: block;
        #menu {
          // position: relative;
          position: sticky;
          z-index: 1000;
          display: block;
          // color: black;
        }
      }
      .tPart {
        width: 80vw;
        .search {
          width: 100%;
        }
        #search {
          border-radius: 1.5rem;
          padding: 1.5rem;
          width: 100%;
        }
        .searchIcon {
          font-size: 20px;
          position: absolute;
          top: 6px;
          cursor: pointer;
          right: 0;
          color: purple;
        }
      }
    }
  }
  // .features .featuresCol .box p {
  //   font-size: 10px !important;
  // }

  /* Inline #13 | http://localhost:3000/login */

  .login {
    /* top: 50%; */
    top: 29% !important;
  }
  // For changing the login button
  header nav .btns .getS:nth-child(1) {
    background-color: black;
    color: white;
  }
  .getS {
    font-size: 12px !important;
    padding: 5px 10px !important;
    transition: display 0.5s !important;
  }
}
.active {
  // right: 50vw;
  color: black;
  // left: 50vw;
  transform: translateX(0%);
  // transition: all 2s;
}
.bactive {
  display: flex !important;
}
.bunactive {
  display: none !important;
}
.unactive {
  display: none;
}
@media screen and (max-width: 500px) {
  .menu {
    display: none;
    // width: 70rem;
    flex: 1 1 40rem !important;
  }
}
