header {
  background-image: -webkit-gradient(linear, left top, right top, from(#ff512f), color-stop(51%, #f09819), to(#ff512f));
  background-image: linear-gradient(to right, #ff512f 0%, #f09819 51%, #ff512f 100%);
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 8rem;
  width: 100%;
}

header nav {
  width: 80%;
  margin: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

header nav h2 {
  font-size: 4rem;
}

header nav ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

header nav ul li {
  position: relative;
  margin: 0rem 2rem;
}

header nav ul li .a {
  color: white;
}

header nav ul li a {
  font-size: 1.8rem;
  cursor: pointer;
}

header nav ul li::after {
  position: absoute;
  content: "";
  width: 0%;
  height: 0.3rem;
  background-color: #6d6cfd;
  display: block;
  bottom: -1.5rem;
  left: 0;
  -webkit-transition: width 0.5s;
  transition: width 0.5s;
}

header nav ul li:hover::after {
  width: 100%;
}

header nav .tPart {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

header nav .tPart #search {
  border-radius: 0.5rem;
  padding: 0.5rem;
}

header nav .tPart .searchIcon {
  font-size: 2rem;
  position: absolute;
  top: 3px;
  cursor: pointer;
  right: 0;
  color: purple;
}

header nav .btns {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

header nav .btns .getS {
  margin-left: 1.5rem;
  background-color: #6d6cfd;
  color: white;
  padding: 0.6rem 1rem;
  font-size: 1.5rem;
  border-radius: 0.5rem;
  background: #d878dd;
  margin: 10px;
  text-align: center;
  text-transform: uppercase;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  -webkit-box-shadow: 0 0 20px #eee;
          box-shadow: 0 0 20px #eee;
  border-radius: 10px;
  display: block;
  text-transform: capitalize !important;
}

header nav .btns .getS:nth-child(1) {
  background-color: white;
  color: black;
}

header nav .btns .getS:nth-child(1):hover {
  color: #d878dd;
}

header nav .btns .getS:hover {
  background-position: right center;
  /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}

header nav .menu {
  display: none;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 70rem;
          flex: 1 1 70rem;
}

header nav .menu #menu {
  font-size: 4rem;
  cursor: pointer;
  display: none;
  float: right;
}

header .logout {
  cursor: pointer;
  margin-left: 1.5rem;
  background-color: #6d6cfd;
  color: white;
  padding: 0.6rem 1rem;
  font-size: 1.5rem;
  border-radius: 0.5rem;
  background: white;
  margin: 10px;
  text-align: center;
  text-transform: uppercase;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  background-size: 200% auto;
  color: black;
  -webkit-box-shadow: 0 0 20px #eee;
          box-shadow: 0 0 20px #eee;
  border-radius: 10px;
  display: block;
  text-transform: capitalize !important;
}

header .logout:hover {
  background-position: right center;
  /* change the direction of the change here */
  color: #d878dd;
  text-decoration: none;
}

@media screen and (max-width: 768px) {
  header {
    height: 16rem;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
  }
  header nav {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  header nav ul {
    padding-top: 8rem;
    z-index: 100;
    position: absolute !important;
    height: 100vh !important;
    -webkit-transform: translateX(150%);
            transform: translateX(150%);
    width: 50vw;
    background-color: white;
    right: 0;
    top: 0;
    color: black;
    display: block;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
  }
  header nav ul .a {
    color: black !important;
    font-size: 3rem;
  }
  header nav ul li {
    padding: 2rem;
  }
  header nav .btns {
    position: absolute !important;
    right: 0%;
    top: 27%;
    z-index: 101;
  }
  header nav .menu {
    display: block;
  }
  header nav .menu #menu {
    position: -webkit-sticky;
    position: sticky;
    z-index: 1000;
    display: block;
  }
  header nav .tPart {
    width: 80vw;
  }
  header nav .tPart .search {
    width: 100%;
  }
  header nav .tPart #search {
    border-radius: 1.5rem;
    padding: 1.5rem;
    width: 100%;
  }
  header nav .tPart .searchIcon {
    font-size: 20px;
    position: absolute;
    top: 6px;
    cursor: pointer;
    right: 0;
    color: purple;
  }
  /* Inline #13 | http://localhost:3000/login */
  .login {
    /* top: 50%; */
    top: 29% !important;
  }
  header nav .btns .getS:nth-child(1) {
    background-color: black;
    color: white;
  }
  .getS {
    font-size: 12px !important;
    padding: 5px 10px !important;
    -webkit-transition: display 0.5s !important;
    transition: display 0.5s !important;
  }
}

.active {
  color: black;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.bactive {
  display: -webkit-box !important;
  display: -ms-flexbox !important;
  display: flex !important;
}

.bunactive {
  display: none !important;
}

.unactive {
  display: none;
}

@media screen and (max-width: 500px) {
  .menu {
    display: none;
    -webkit-box-flex: 1 !important;
        -ms-flex: 1 1 40rem !important;
            flex: 1 1 40rem !important;
  }
}
