@import "../colors";
@import "../utils";
.pop {
  @include flex(center);
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 100;
  background-color: black;
  opacity: 0.9;
  color: white;
  .popBox {
    .messege {
      font-size: 3rem;
    }
    position: relative;
    #close {
      position: absolute;
      font-size: 2rem;
      top: 1rem;
      right: 1rem;
      cursor: pointer;
    }
    @include flex(center);
    height: 20rem;
    width: 30rem;
    border: 0.2rem solid white;
    border-radius: 1rem;
  }
}
